<template>
  <div class="shipmentList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        
        <!-- <v-img :src="logoImage" aspect-ratio="2.5" contain></v-img> -->
        <v-spacer></v-spacer>
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label=""
            v-model="searchKey"
            v-if="auth.shipment_search_key"
          ></v-text-field>
          <!-- fileinput -->
          <input
            type="file"
            accept="image/*"
            ref="fileInput"
            @change="onFileSelected"
            style="display: none"
          />

          <ShipmentTimeOption class="mx-1" />

          <popupadd
            ref="editProcess"
            :auth="auth"
            :shipmentItems="items"
            @getAllData="getAllData"
            v-if="auth.shipment_add_key"
          ></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
          locale="zh-tw"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left"></th>
                    <th class="text-left">/</th>
                    <th class="text-left"></th>
                    <th class="text-left"></th>
                    <th class="text-left"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="" />
                          </v-btn>
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.shipment_edit_key"
                          >
                            <v-list-item-title></v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.shipment_del_key"
                          >
                            <v-list-item-title></v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="print(item)"
                          >
                            <v-list-item-title></v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>

                    <td>{{ item.date }}</td>
                    <td>{{ item.customer.text }}</td>
                    <td>{{ item.code }}</td>
                    <td>
                      {{ item.create_man }}
                    </td>
                    <td>
                      {{ item.edit_man }}
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{items}}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/ShipmentAdd";
import ShipmentTimeOption from "@/components/ShipmentTimeOption";
import dayjs from "dayjs";

export default {
  components: { popupadd, ShipmentTimeOption },
  data() {
    return {
      items: [], //
      searchKey: "",
      auth: "", //(mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
      logoImage: require("@/assets/img/car_kei_truck_horo.png"),
      picBaseUrl: this.$store.state.base_url + "/upload/shipment/",
      selectedFile: "",
      uploadItem: "",
    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";

      let match = keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
      
      match = match.sort((a,b)=>{
        return a.date < b.date ? 1 : -1;
      })

      return match;
    },
  },

  methods: {
    getAllData() {
      //
      this.myAxios(
        "general/getAll/" + this.$store.state.databaseName + "/shipment"
      ).then((rs) => {
        this.items = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    //
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //
    del(item) {
      //yn() main.js
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "shipment",
            datalist: JSON.stringify(item),
          };

          let url =
            "general/delv3/" +
            this.$store.state.databaseName +
            "/" +
            postData.tablename;

          this.myAxios(url, postData).then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "!" });
              this.getAllData();
            }
          });

          // -> 
          if (item.picname != "lazypic.jpg") {
            console.log("delpic");
            url =
              "general/delPic/" +
              this.$store.state.databaseName +
              "/" +
              postData.tablename +
              "/" +
              item.picname;
            this.myAxios(url, postData).then((rs) => {
              console.log(rs);
            });
          }
        }
      });
    },
    //
    print(item){
      let routeData = this.$router.resolve({path: "/print/shipmentprint",query:{data:JSON.stringify(item)}});
      window.open(routeData.href, "_blank");
    },
    //
    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
      var extension = this.selectedFile.name.split(".").pop().toLowerCase(); //
      var fitType = ["gif", "png", "jpg", "jpeg"];

      var errorMsg = ""; //
      if (fitType.indexOf(extension) == -1) {
        errorMsg = ":" + this.selectedFile.name + "";
      }
      if (this.selectedFile.size > 1024 * 1024 * 5) {
        //-5MB
        errorMsg = ":" + this.selectedFile.name + "5M";
      }

      if (errorMsg == "") {
        this.onUpload();
      } else {
        this.$swal({ icon: "error", text: errorMsg });
        this.selectedFile = "";
      }
    },
    //&
    onUpload() {
      // -> 
      const fd = new FormData();
      fd.append("snkey", this.uploadItem.snkey); //snkey
      fd.append("image", this.selectedFile);

      this.axios
        .post(
          "general/upload/" + this.$store.state.databaseName + "/shipment",
          fd
        )
        .then((rs) => {
          console.log(rs.data);
          this.$refs.fileInput.value = "";
          this.items.some((item) => {
            if (item.snkey == this.uploadItem.snkey) {
              item.picname = rs.data.picname;
            }
          });

          //
          let postData = {
            snkey: this.uploadItem.snkey,
            datalist: JSON.stringify(this.uploadItem),
          };

          this.myAxios(
            "general/edit/" + this.$store.state.databaseName + "/shipment",
            postData
          ).then((rs) => {
            console.log(rs);
          });
        });
    },
  },
};
</script>


<style lang="scss">
.shipmentList {
  .theme--light.v-data-table
    > .v-data-table__wrapper
    > table
    > thead
    > tr
    > th {
    font-size: 1rem;
  }
}
</style>